import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_DISABLE_AUTOFIT_WIDTH = Symbol('TABLE_DISABLE_AUTOFIT_WIDTH');

export function setTableAutofitWidthDisable(table: Table) {
  return {
    type: TABLE_DISABLE_AUTOFIT_WIDTH,
    payload: {
      node: table,
    },
  };
}

interface TableSetTableAutofitWidthDisablePayload {
  node: Table;
  delta: number;
  maxWidth?: number;
}

interface TableSetTableAutofitWidthDisableAction {
  type: typeof TABLE_DISABLE_AUTOFIT_WIDTH;
  payload: TableSetTableAutofitWidthDisablePayload;
}

export function isTableSetTableAutofitWidthDisableAction(
  action: Action,
): action is TableSetTableAutofitWidthDisableAction {
  return action.type === TABLE_DISABLE_AUTOFIT_WIDTH;
}
